<template>
  <div class="container">
    <header>
      <h3>Hey there!</h3>
    </header>
    <p>My name is Karl. Before you can read more about me, I would ask you to please log in to this site. If you don't have an account, you can register.</p>
    <p>To do either of those things, please use the buttons in the top-right corner.</p>
  </div>
</template>

<script>
import UserService from "../services/user.service";

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Home",
  data() {
    return {
      content: "",
    };
  },
  mounted() {
    UserService.getPublicContent().then(
        (response) => {
          this.content = response.data;
        },
        (error) => {
          this.content =
              (error.response &&
                  error.response.data &&
                  error.response.data.message) ||
              error.message ||
              error.toString();
        }
    );
  },
};
</script>

<style scoped>
html {
  background: none;
}
header {
  padding-top: 20px;
}
</style>